body {
  margin:0;
  touch-action: none; /* allow vertical scroll but prevent swipe to go back in msEdge */
  background-color: lightgrey;
  font-family: Roboto;
  overflow: hidden;
}

.wrapper {
  overflow-y: scroll
}

.container {
  height:100vh;
  width:calc(50% - 3px);
  overflow-x:visible;
  touch-action: pan-y;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.list-item {
  width:100%;
  height:50px;
  margin: 2px 2px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: whitesmoke;
}

.drag-style {
  background-color: orange;
}
.shadow {
    box-shadow: 0px 5px 5px -3px rgba(0, 0, 0, .2),
    0px 8px 10px 1px rgba(0, 0, 0, .14),
    0px 3px 14px 2px rgba(0, 0, 0, .12);
}

